body {
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  color: $body-color;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Raleway', sans-serif;
}

.pre-heading {
  font-weight: 600;
  font-size: 11px;
  line-height: 1.4;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: $primary-title-color-dk;

    margin-bottom: 20px;

    &::before {
        content: '';
        display: inline-block;
        width: 60px;
        height: 6px;
        border-top: 1px solid $primary-title-color-dk;
        margin-right: 20px;
    }

  margin-bottom: 20px;

}

.section-title {
  font-weight: 700;
  font-size: 42px;
  line-height: 1.17;
  letter-spacing: 0.05em;

  color: $primary-title-color-lt;
}
