.container {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding-left: 31px;
    padding-right: 31px;
  
    @media screen and (min-width: 480px) {
      width: 480px;
    }
  
    @media screen and (min-width: 768px) {
      width: 768px;
      padding-left: 39px;
      padding-right: 39px;
    }
  
    @media screen and (min-width: 1200px) {
      width: 1200px;
      padding-left: 15px;
      padding-right: 15px;
    }
}