.pre-heading--gallery {
  color: #303030;
  position: relative;
  vertical-align: middle;

  margin-left: 80px;
}

@media screen and (max-width: 767px) {
  .pre-heading--gallery {
    line-height: 1.9;
  }
}

@media screen and (max-width: 767px) {
  .gallery__item:not(:last-child) {
    margin-bottom: 30px;
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .gallery {
    display: flex;
    flex-wrap: wrap;
    margin: -15px;
  }

  .gallery__item {
    width: calc((100% - 2 * 30px) / 2);
    margin: 15px;
  }
}

@media screen and (min-width: 1200px) {
  .gallery__item {
    width: calc((100% - 4 * 30px) / 4);
  }
}

