.pre-heading {
  &.price {
    font-weight: 600;
    font-size: $fs-text-mini;
    line-height: 1.36;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: $secondary-text-color;

    margin-bottom: 20px;
  }
}

.button-price__position {
  text-align: center;
}

.price-item__text {
  font-weight: normal;
  font-size: $fs-text-small;
  line-height: 30px;
  letter-spacing: 0.02em;
  color: $primary-text-color-lt;
}

.price-items {
  padding: 30px;
  background: linear-gradient(180deg, #171a24 0%, rgba(23, 26, 36, 0.2) 100%);
  margin-bottom: 50px;
}

.section-title {
  &.price {
    margin-bottom: 44px;
  }
}
.price-item {
  &:last-child {
    margin-bottom: 0;
  }
  margin-bottom: 20px;
}

.layout.price {
  background-image: linear-gradient(
      rgba(17, 19, 25, 0.8),
      rgba(17, 19, 25, 0.8)
    ),
    url(../images/slider2-mob@2x.jpg);
  background-repeat: no-repeat;
}

@media screen and (min-width: 1200px) {
  .price-items {
    display: none;
  }
}

@media screen and (max-width: 1200px) {
  .price-items.v--desc {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .price-items.v--desc {
    display: flex;
  }
  .price-items {
    padding: 0;
  }
  .price-item-desc {
    &.secondary--gradient {
      background: linear-gradient(
        180deg,
        #090b13 0%,
        rgba(9, 11, 19, 0.2) 100%
      );
    }
    padding: 30px;
    color: $primary-text-color-lt;
    width: 585px;
  }
}

@media screen and (min-width: 480px) {
  .layout.price {
    background-image: linear-gradient(
        rgba(17, 19, 25, 0.8),
        rgba(17, 19, 25, 0.8)
      ),
      url(../images/slider2-tab.jpg);
  }
}

@media screen and (min-width: 768px) {
  .layout.price {
    background-image: linear-gradient(
        rgba(17, 19, 25, 0.8),
        rgba(17, 19, 25, 0.8)
      ),
      url(../images/slider2-tab@2x.jpg);
  }
}

@media screen and (min-width: 1200px) {
  .layout.price {
    background-image: linear-gradient(
        rgba(17, 19, 25, 0.8),
        rgba(17, 19, 25, 0.8)
      ),
      url(../images/slider2-des.jpg);
  }
}
